.page-body {
  font-family: "PT Mono Regular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #666666;
  height: 100%;
}

.center-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.center-container--fixed {
  position: fixed;
}

.page-header {
  min-width: 100%;
  z-index: 3;
  margin: 0;
  padding: 31px 50px 0;
}
@media (max-width: 1023px) {
  .page-header {
    padding: 21px 25px 0 25px;
  }
}
@media (max-width: 767px) {
  .page-header {
    padding: 18px 15px 0;
  }
}

.page-header--change-position {
  position: unset;
}

.page-header__inner {
  display: flex;
  justify-content: space-between;
  max-width: 1420px;
  margin: 0 auto;
  padding: 0;
}

.page-header__logo-block {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
}

.page-header__logo-block a {
  margin: 0 16px 0 0;
  padding: 0;
}

@media (max-width: 767px) {
  .page-header__logo-block img {
    width: 38px;
    height: 38px;
  }
}

@media (max-width: 767px) {
  .page-header__logo-block img[src^="img/desktop"] {
    display: none;
  }
}

.page-header__logo-block img[src^="img/mobile"] {
  display: none;
}
@media (max-width: 767px) {
  .page-header__logo-block img[src^="img/mobile"] {
    display: block;
  }
}

.page-header__contacts {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  max-width: 1000px;
  margin: 0;
  padding: 0 5px 0 0;
}
@media (max-width: 767px) {
  .page-header__contacts {
    padding: 0;
  }
}

.page-header__contacts p:first-of-type {
  position: relative;
  max-width: 151px;
  margin: 0 67px 0 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__contacts p:first-of-type {
    margin: 0;
  }
}
.page-header__contacts p:first-of-type::before {
  content: "";
  position: absolute;
  top: 0;
  left: 11px;
  background-image: url("../img/desktop/mail-icon.svg");
  background-size: 19px 14px;
  width: 19px;
  height: 14px;
}
@media (max-width: 1023px) {
  .page-header__contacts p:first-of-type::before {
    visibility: visible;
    -webkit-mask: url("../img/tablet/mail-icon.svg");
    mask: url("../img/tablet/mail-icon.svg");
    -webkit-mask-size: cover;
    mask-size: cover;
    background-color: #00b2ff;
    background-size: 600px;
    width: 25px;
    height: 19px;
    top: 0;
    left: -76px;
  }
}
@media (max-width: 767px) {
  .page-header__contacts p:first-of-type::before {
    left: -73px;
  }
}

.page-header__contacts p:last-of-type {
  position: relative;
  max-width: 288px;
  margin: 0 40px 0 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-header__contacts p:last-of-type {
    margin: 0;
  }
}
.page-header__contacts p:last-of-type::before {
  content: "";
  position: absolute;
  top: 0;
  left: 12px;
  background-image: url("../img/desktop/phone-icon.svg");
  background-size: 18px 18px;
  width: 18px;
  height: 18px;
}
@media (max-width: 1023px) {
  .page-header__contacts p:last-of-type::before {
    visibility: visible;
    -webkit-mask: url("../img/tablet/phone-icon.svg");
    mask: url("../img/tablet/phone-icon.svg");
    -webkit-mask-size: cover;
    mask-size: cover;
    background-color: #00b2ff;
    background-size: 300px;
    width: 24px;
    height: 24px;
    top: -4px;
    left: -18px;
  }
}
@media (max-width: 767px) {
  .page-header__contacts p:last-of-type::before {
    left: -25px;
  }
}

.page-header__contacts a {
  font-family: "PT Mono Regular", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #666666;
  position: relative;
  color: white;
  margin: 0;
  padding: 10px 10px 10px 45px;
}
.page-header__contacts a:hover, .page-header__contacts a:focus {
  color: #00b2ff;
}
@media (max-width: 1023px) {
  .page-header__contacts a:hover::before, .page-header__contacts a:focus::before {
    background-color: white;
  }
}
@media (max-width: 1023px) {
  .page-header__contacts a {
    font-size: 0;
    color: transparent;
    padding: 0;
  }
}

.page-header__contacts a:active {
  color: #309bd4;
}
@media (max-width: 1023px) {
  .page-header__contacts a:active::before {
    background-color: #309bd4;
  }
}

.page-header__contacts button {
  font-family: "Roboto Condensed Bold", "Arial", sans-serif;
  font-size: 25px;
  line-height: 29px;
  color: white;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid white;
  border-radius: 5px;
  color: white;
  max-width: 180px;
  min-width: 180px;
  cursor: pointer;
  margin: 0;
  padding: 12px;
}
.page-header__contacts button:hover, .page-header__contacts button:focus {
  background-color: #00b2ff;
  border: 0;
}
.page-header__contacts button:active {
  background-color: #309bd4;
}
@media (max-width: 1023px) {
  .page-header__contacts button {
    display: none;
  }
}

.page-main {
  flex: 1;
  min-width: 100%;
  margin: -87px 0 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-main {
    margin-top: -77px;
  }
}
@media (max-width: 767px) {
  .page-main {
    margin-top: -60px;
  }
}

.page-footer {
  min-width: 100%;
  margin: 0;
  padding: 65px 0 0;
}
@media (max-width: 1023px) {
  .page-footer {
    padding: 18px 0 0;
  }
}
@media (max-width: 767px) {
  .page-footer {
    padding: 46px 0 0;
  }
}

.page-footer__contacts-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1520px;
  margin: 0 auto 55px auto;
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .page-footer__contacts-wrapper {
    flex-direction: column;
    margin: 0 auto 36px;
    padding: 0;
  }
}
@media (max-width: 1023px) and (min-width: 768px) {
  .page-footer__contacts-wrapper {
    display: block;
  }
}
@media (max-width: 767px) {
  .page-footer__contacts-wrapper {
    display: block;
    margin-bottom: 0;
  }
}

.page-footer__logo-socials-block {
  max-width: 220px;
  margin: 0 80px 0 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .page-footer__logo-socials-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    min-width: 100%;
    max-height: 200px;
    border-bottom: 1px solid rgba(98, 105, 98, 0.2);
    margin-bottom: 38px;
    padding: 0 25px 24px;
  }
}
@media (max-width: 767px) {
  .page-footer__logo-socials-block {
    justify-content: space-between;
    margin: 0 0 45px;
    padding: 0 25px 0 44px;
    border: 0;
  }
}
@media (max-width: 638px) {
  .page-footer__logo-socials-block {
    flex-direction: column;
    justify-content: center;
  }
}

.page-footer__logo-socials-block > p {
  display: none;
}
@media (max-width: 1023px) {
  .page-footer__logo-socials-block > p {
    display: block;
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.04em;
    transform: translate(17px, 3px);
    text-transform: uppercase;
    margin: 0;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .page-footer__logo-socials-block > p {
    margin-right: 10px;
    transform: unset;
  }
}
@media (max-width: 638px) {
  .page-footer__logo-socials-block > p {
    margin-bottom: 31px;
  }
}

.logo-block {
  display: flex;
  margin: 0;
  padding: 0;
}
@media (max-width: 657px) {
  .logo-block {
    margin-bottom: 22px;
  }
}
@media (max-width: 767px) {
  .logo-block {
    transform: translateX(-18px);
  }
}

.logo-block a {
  margin: 0 0 54px 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .logo-block a {
    margin: 0;
  }
}

.socials {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  max-width: 150px;
}
@media (max-width: 1023px) {
  .socials {
    transform: translate(4px, 5px);
  }
}
@media (max-width: 638px) {
  .socials {
    transform: translate(-10px, 1px);
  }
}

.socials a {
  margin-right: 18px;
  margin-bottom: 10px;
}
.socials a:last-child {
  margin-right: 0;
}

.socials-facebook {
  position: relative;
  cursor: pointer;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .socials-facebook {
    margin: 0 15px 0 0;
  }
}
.socials-facebook::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  -webkit-mask: url("../img/facebook.svg");
  mask: url("../img/facebook.svg");
  -webkit-mask-size: cover;
  mask-size: cover;
  background-color: #cdcdcd;
  background-size: 26px 26px;
  width: 26px;
  height: 26px;
}

.socials-facebook:hover::before,
.socials-facebook:focus::before {
  background-color: #4267b2;
}

.socials-facebook:active::before {
  background-color: #243e70;
}

.socials-classmates {
  position: relative;
  cursor: pointer;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) {
  .socials-classmates {
    margin: 0 15px 0 0;
  }
}
.socials-classmates::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  left: 0;
  -webkit-mask: url("../img/classmates.svg");
  mask: url("../img/classmates.svg");
  -webkit-mask-size: cover;
  mask-size: cover;
  background-color: #cdcdcd;
  background-size: 26px 26px;
  width: 26px;
  height: 26px;
}

.socials-classmates:hover::before,
.socials-classmates:focus::before {
  background-color: #ed812b;
}

.socials-classmates:active::before {
  background-color: #bb6a28;
}

.socials-vk {
  position: relative;
  cursor: pointer;
  margin: 0;
  padding: 0;
}
.socials-vk::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  -webkit-mask: url("../img/vk.svg");
  mask: url("../img/vk.svg");
  -webkit-mask-size: cover;
  mask-size: cover;
  background-color: #cdcdcd;
  background-size: 27px 26px;
  width: 27px;
  height: 26px;
}

.socials-vk:hover::before,
.socials-vk:focus::before {
  background-color: #4c75a3;
}

.socials-vk:active::before {
  background-color: #3c5a7c;
}

.address {
  transform: translateX(-4px);
  margin: 0;
  padding: 0;
}
@media (max-width: 1023px) and (min-width: 768px) {
  .address {
    display: inline-grid;
    transform: translate(calc(100vw - 708px));
  }
}

.address h3 {
  font-family: "Roboto Condensed Regular", "Arial", sans-serif;
  font-size: 22px;
  line-height: 34px;
  color: #00b2ff;
  position: relative;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #1b2d37;
  margin: 0 0 29px 0;
  padding: 0;
}
@media (max-width: 767px) {
  .address h3 {
    cursor: pointer;
    min-width: 100%;
    margin: 0;
    padding: 28px 15px 36px 15px;
  }
  .address h3::before {
    content: "";
    display: none;
    position: absolute;
    top: 28px;
    right: 21px;
    background-size: 1px 16px;
    width: 1px;
    height: 16px;
    border-right: 1px solid #c4c4c4;
  }
  .address h3::after {
    content: "";
    position: absolute;
    top: 36px;
    right: 14px;
    background-size: 16px 1px;
    width: 16px;
    height: 1px;
    border-bottom: 1px solid #c4c4c4;
  }
}
@media (max-width: 767px) {
  .address h3 {
    padding: 28px 18px 36px;
  }
}

.address h3.address--plus-appear {
  max-width: 333px;
}
@media (max-width: 767px) {
  .address h3.address--plus-appear::before {
    display: block;
  }
}

@media (max-width: 767px) {
  .address__inner {
    display: none;
    margin-bottom: 26px;
    padding: 0 17px;
  }
}

.address__inner--appear {
  display: block;
}

.address__inner p {
  font-size: 15px;
  line-height: 23px;
  max-width: 215px;
  margin: 0;
  padding: 0;
}
.address__inner p:first-child {
  max-width: 350px;
}

.address__inner a {
  font-size: 15px;
  line-height: 25px;
  color: #666666;
  margin: 0;
  padding: 0;
}
.address__inner a:hover, .address__inner a:focus {
  color: #00b2ff;
}
.address__inner a:active {
  color: #4267b2;
}

.partitions {
  transform: translateX(-48px);
  margin: 0;
  padding: 0 0 0 10px;
}
@media (max-width: 1023px) {
  .partitions {
    transform: unset;
    padding: 0 0 0 23px;
    margin: 0;
  }
}
@media (max-width: 1023px) and (min-width: 768px) {
  .partitions {
